import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			src: 'rtmp://58.200.131.2:1935/livetv/hunantv',
			list: [],
			currentRate: 1, //当前播放id
			rateShow: false, //右侧弹出选项
			detailData: {},
			isMenu: false,
			isshow: false,
			isMenu1: true,
			isIos: true,
			feedbackContent: '',
			isPoster:true,
			videoUrl:'',
			userInfo:{}
		};
	},
	onLoad(options) {
		console.log(JSON.parse(decodeURIComponent(options.data)));
		let data=JSON.parse(decodeURIComponent(options.data))
		this.detailData = data
		
		console.log('this.detailData');
		console.log(this.detailData);
		
		//获取视频接口
		this.geturl()
		//获取视频列表
		this.loadNewsList();
		
	},
	mounted(){
		//获取个人信息
		this.getMsg()
		uni.showLoading({
			title: 'loading'
		});
	},
	onReady: function() {
		this.videoContext = uni.createLivePlayerContext('myVideo', this);
		this.videoContext.play();
	},
	methods: {
		geturl(){
			var obj = { id: this.detailData.id,playrealUrl:""};
			
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-ssjksp-x-hqsp.htm', { params: obj })
				.then(res => {
					console.log('视频url');
					console.log(res);
					if (res.data.msgState == 1) {
						
						this.videoUrl = res.data.rtmpSrc;
						// console.log(this.list);
					} else {
						this.videoContext.stop();
						uni.showToast({
							icon: 'none',
							title: '视频已下线！',
							duration: 2000
						});
						setTimeout(()=>{
							this.backup()
						},2000)
						
					}
				})
				.catch(err => {
					console.log(err);
					uni.showToast({
						icon: 'none',
						title: '获取视频失败！',
						duration: 2000
					});
				});
		},
		//获取个人中心基本信息
		getMsg(){
			
			console.log("个人数据")
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-grzxsj.htm')
				.then(res => {
					
					console.log(res);
					if (res.data.msgState == 1) {
						var userInfo=res.data.list[0]
						this.userInfo=userInfo
						this.userInfo.unit=this.userInfo.unit.split('-').join('')
						
						
					}else{
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:"none"
						});
					}
					
				})
				.catch(err => {
					console.log(err);
					uni.showToast({
						title: "请求失败",
						duration: 2000,
						icon:"none"
					});
				});
		},
		fullscreenchange(event) {
			console.log(event.detail.fullScreen);

			// 	if (!event.detail.fullScreen) {
			// 		this.backup()
			// 	}
		},
		statechange(e) {
			
			console.log('live-player code:', e.detail.code);
			if (e.detail.code == 2001) {
				uni.showLoading({
					title: 'loading'
				});
			}
			if (e.detail.code == 2003) {
				this.isPoster=false;
				uni.hideLoading();
				this.videoContext.requestFullScreen({ direction: 90 });
			}
			if (e.detail.code == 2105) {
				this.isPoster=false;
				uni.hideLoading();
				//this.videoContext.requestFullScreen({ direction: 90 });
			}
			if (e.detail.code == -2301) {
				uni.showToast({
					icon: 'none',
					title: '网络断连，请重新退出连接！',
					duration: 2000
				});
			}
			//uni.hideLoading();
		},
		backup() {
			uni.navigateBack({
				delta: 1
			});
		},
		clk() {
			let that = this;
			that.isMenu = !that.isMenu;
		},
		loadNewsList() {
			console.log(this.detailData);
			var obj = { pageNo: 1, pageCount: 1000 };

			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-ssjksp-x.htm', { params: obj })
				.then(res => {
					//console.log('监控列表数据');
					//console.log(res);
					if (res.data.msgState == 1) {
						this.list = res.data.list;
						console.log(this.list);
					} else {
						uni.showToast({
							icon: 'none',
							title: '监控列表获取失败！',
							duration: 2000
						});
					}
				})
				.catch(err => {
					console.log(err);
					uni.showToast({
						icon: 'none',
						title: '监控列表获取失败！',
						duration: 2000
					});
				});
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.detail.errMsg,
				showCancel: false
			});
		},
		//一键求助弹出框

		sosPopup() {
			console.log('我要反馈');
			this.videoContext.exitFullScreen();
			this.$nextTick(() => {
				this.$refs.showtip1.open();
				this.isshow = true;
				this.isMenu1 = true;
			});
		},
		cancel(type) {
			this.$refs.showtip1.close();
			this.videoContext.requestFullScreen({ direction: 90 });
			this.isshow = false;
		},
		//一键求助
		submitHelp() {
			if(this.feedbackContent==''){
				uni.showToast({
				    title: '请输入求助内容',
				    duration: 2000,
					icon:"none"
				});
				return
			}
			var obj = { note: this.feedbackContent, id: this.detailData.id };
			//发起请求
			console.log('一键求助');
			uni.showLoading({
				title: 'loading'
			});
			this.$http
				.post('/interface/rest/http/xlwb/xlgc-wb-xcx-yjqz-x.htm', obj)
				.then(res => {
					uni.hideLoading();
					console.log(res.data);
					if (res.data.msgState == 1) {
						uni.showToast({
							 title: res.data.msg,
							 duration: 2000,
							
						})
					}else{
						uni.showToast({
							 title: res.data.msg,
							 duration: 2000,
							 icon:'none'
						})
					}
				
					setTimeout(() => {
						this.cancel();
					}, 1000);
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
				});
		},
		//侧边导航栏
		choosed() {
			this.isMenu1 = false;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.isIos = false;
				this.videoContext.exitFullScreen();
			}
		},
		closeedMenu() {
			this.isMenu1 = true;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.videoContext.requestFullScreen({direction:90});
			}
		},
		//选择视频播放
		switchRate(val) {
			let that = this;
			
		this.$nextTick(() => {
			that.detailData = val;
			that.isPoster=true;
			that.geturl()
		});
			
			uni.showLoading({
				title: 'loading'
			});
			this.isMenu1 = true;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.videoContext.requestFullScreen({direction:90});
			}
		}
	}
};
